











import { Component, Prop, Vue } from "vue-property-decorator";
import Dialog from './Dialog.vue';

@Component({
    components: {
        Dialog,
    }
})
export default class Prompt extends Vue {
    @Prop() title!: string;
    @Prop() message!: string;
    @Prop() initialValue!: string;

    model = '';

    created(): void {
        this.model = this.initialValue;
    }
}
